import { React, useRef, useEffect } from "react";

var screenwidth;
var screenheight;
screenheight = window.outerHeight;
screenwidth = window.outerWidth;
console.log(window);
var stage = {
  w: screenwidth - 10,
  h: screenheight - 100,
};
var pointer = {
  x: stage.w / 2 + 400,
  y: stage.h / 2,
};
var Cannon = function (x, y, tx, ty) {
  this.x = x;
  this.y = y;
  this.tx = tx;
  this.ty = ty;
  this.r = 10;
};
var cannons = [];
var Enemy = function () {
  if (screenwidth > 700) {
    this.x = stage.w / 2 + 400;
  } else {
    this.x = stage.w / 2;
  }
  this.y = stage.h / 2;
  this.r = 10;
  this.tx = Math.floor(Math.random() * stage.w);
  this.ty = Math.floor(Math.random() * stage.h);
  this.des = false;
  this.eyeX = 0.4;
  this.eyeY = 0.25;
  this.eyeR = 0.25;
  this.sp = 50;
  this.spl = 1.4;
  this.op = 1;
  this.danger = false;
  this.nuked = false;
};
var enemies = [];
for (var i = 0; i < 2; i++) {
  enemies[i] = new Enemy();
}
enemies.push(new Enemy());
enemies.push(new Enemy());
enemies.push(new Enemy());
var entm = 0;
function newGame() {
  score = 0;
  health = 100;
  enemies = [];
  enemies.push(new Enemy());
  enemies.push(new Enemy());
  enemies.push(new Enemy());
  enemies.push(new Enemy());
  enemies.push(new Enemy());
  againprog = 0;
}

var steptime = 0;
if (screenwidth > 700) {
  var Star = function () {
    this.a = Math.random() * Math.PI * 2;
    this.v = Math.random() * 5;
    this.x = stage.w / 2 + 400;
    this.y = stage.h / 2;
    this.r = 0.2;
  };
} else {
  var Star = function () {
    this.a = Math.random() * Math.PI * 2;
    this.v = Math.random() * 5;
    this.x = stage.w / 2;
    this.y = stage.h / 2;
    this.r = 0.2;
  };
}
var stars = [];
for (var i = 0; i < 300; i++) {
  stars[i] = new Star();
  var str = stars[i];
  var move = Math.random() * 1;

  str.x += Math.sin(str.a) * move;
  str.y += Math.cos(str.a) * move;
}
var loffset = 0;
var toffset = 0;
var scale = 1;
var square = { x: stage.w, y: stage.h };
var firetm = 0;
var fireact = true;
var ctx;
var _pexcanvas;
var healthprog = 0;
var againprog = 0;
var firetm = 0;
var fireact = true;
var health = 100;
var score = 0;
var dangera;
var danger;
var gameover;

export default function GameL() {
  var canv = useRef(null);
  useEffect(() => {
    var el2 = canv.current;
    _pexcanvas = el2;
    _pexcanvas.width = stage.w;
    _pexcanvas.height = stage.h;
    ctx = _pexcanvas.getContext("2d");
    ctx.lineWidth = 1;
    function target() {
      steptime = Date.now();
      ctx.clearRect(0, 0, stage.w, stage.h);

      for (var i = 0; i < stars.length; i++) {
        var str = stars[i];

        str.x += Math.sin(str.a) * str.v;
        str.y += Math.cos(str.a) * str.v;
        str.r += str.v / 200;

        ctx.beginPath();
        ctx.arc(str.x, str.y, str.r, 0, 2 * Math.PI);
        ctx.fillStyle = "rgba(107, 107, 107,1)";
        ctx.fill();

        if (str.x > stage.w || str.x < 0 || str.y < 0 || str.y > stage.h) {
          stars[i] = new Star();
        }
      }

      if (!gameover) {
        danger = false;

        entm++;
        if (enemies.length < 10 && entm > 300) {
          entm = 0;
          enemies.push(new Enemy());
        }
        ctx.lineWidth = 2;
        for (var i = 0; i < enemies.length; i++) {
          var en = enemies[i];
          if (!en.danger) {
            ctx.strokeStyle = "rgba(180, 113, 36," + en.op + ")";
            ctx.fillStyle = "rgba(180, 113, 36," + en.op + ")";
          } else {
            health -= 0.01;
            ctx.strokeStyle = "rgba(255,0,0," + en.op + ")";
            ctx.fillStyle = "rgba(180, 113, 36," + en.op + ")";
            danger = true;
          }

          if (!en.des) {
            if (en.danger) {
              // var randx = Math.floor(Math.random() * 4) - 2;
              // var randy = Math.floor(Math.random() * 4) - 2;
            } else {
              en.x += (en.tx - en.x) / 100;
              en.y += (en.ty - en.y) / 100;
            }
            if (screenwidth > 700) {
              en.r += (100 - en.r) / 100;
            } else {
              en.r += (50 - en.r) / 100;
            }
            if (Math.abs(50 - en.r) < 2 && !en.danger) {
              en.danger = true;
            }
            ctx.beginPath();
            ctx.moveTo(en.x - 50, en.y + 50);
            ctx.lineTo(en.x, en.y - 30);
            ctx.lineTo(en.x + 50, en.y + 50);
            ctx.closePath();
            ctx.stroke();
          } else {
            en.eyeR += (0.5 - en.eyeR) / 5;
            en.op += (0 - en.op) / 5;
            // en.sp += (5 - en.sp) / 20;
            en.r += (100 - en.r) / 20;
            en.spl += (2.5 - en.spl) / 5;

            ctx.beginPath();
            ctx.moveTo(en.x - 40, en.y);
            ctx.lineTo(en.x, en.y - 40);
            ctx.lineTo(en.x + 40, en.y);
            ctx.closePath();
            ctx.stroke();
          }

          if (Math.abs(0.5 - en.eyeR) < 0.01) {
            var rand = Math.floor(Math.random() * 2);
            if (enemies[i].nuked && rand == 1) {
              enemies.splice(i, 1);
            } else {
              enemies[i] = new Enemy();
            }
          }
        }
        if (danger) {
          dangera += 0.05 + (100 - health) / 1000;
          if (dangera >= Math.PI) {
            dangera = 0;
          }
          ctx.fillStyle = "rgba(255,0,0," + (1 - Math.sin(dangera)) / 4 + ")";
          ctx.fillRect(0, 0, stage.w, stage.h);
          if (health < 10) {
            ctx.fillStyle = "rgba(255,255,0," + Math.sin(dangera) + ")";
            ctx.strokeStyle = "rgba(255,255,0," + Math.sin(dangera) + ")";

            ctx.lineWidth = 10;
            ctx.beginPath();
            ctx.lineJoin = "round";
            if (screenwidth > 700) {
              ctx.moveTo(stage.w / 2 + 400, stage.h / 4);
              ctx.lineTo(stage.w / 2 + stage.h / 7 + 400, stage.h / 2);
              ctx.lineTo(stage.w / 2 - stage.h / 7 + 400, stage.h / 2);
            } else {
              ctx.moveTo(stage.w / 2, stage.h / 4);
              ctx.lineTo(stage.w / 2 + stage.h / 7, stage.h / 2);
              ctx.lineTo(stage.w / 2 - stage.h / 7, stage.h / 2);
            }
            ctx.closePath();
            ctx.stroke();

            ctx.font = "bold 80px arial";
            ctx.textAlign = "center";
            ctx.textBaseline = "middle";
            if (screenwidth > 700) {
              ctx.fillText("!", stage.w / 2 + 400, stage.h / 2.5);

              ctx.font = "bold 50px arial";
              ctx.fillText("LOW HEALTH", stage.w / 2 + 400, stage.h * 0.6);
            } else {
              ctx.fillText("!", stage.w / 2, stage.h / 2.5);

              ctx.font = "bold 50px arial";
              ctx.fillText("LOW HEALTH", stage.w / 2, stage.h * 0.6);
            }
          }
        } else {
          dangera = 0;
        }

        healthprog += (health - healthprog) / 5;
        ctx.fillStyle = "#b47124";
        ctx.font = "40px arial";
        // ctx.textAlign = "left";
        // ctx.textBaseline = "middle";
        // ctx.fillText("Health: ", 20, 40);

        ctx.fillText("Score: " + score, stage.w - 200, 40);
        // ctx.fillText("Step:   "+(Date.now()-steptime),20,120);
        if (health > 30) {
          ctx.fillStyle = "rgba(180, 113, 36,0.8)";
        } else {
          ctx.fillStyle = "rgba(255,0,0,0.8)";
        }
        ctx.lineWidth = 2;
        ctx.fillRect(stage.w - 150, stage.h - 30, healthprog * 1.5, 30);
        ctx.strokeStyle = "#b47124";
        ctx.strokeRect(stage.w - 150, stage.h - 30, 150, 30);

        if (health < 0) {
          gameover = true;
        }
      } else {
        if (screenwidth > 700) {
          ctx.fillStyle = "rgba(180, 113, 36,0.3)";
          ctx.fillRect(
            (stage.w - 220) / 2 + 400,
            stage.h * 0.65 - 25,
            againprog,
            50
          );
        } else {
          ctx.fillStyle = "rgba(180, 113, 36,0.3)";
          ctx.fillRect((stage.w - 220) / 2, stage.h * 0.65 - 25, againprog, 50);
        }

        ctx.fillStyle = "#b47124";
        ctx.font = "bold 40px arial";
        ctx.textAlign = "center";
        ctx.textBaseline = "middle";
        if (screenwidth > 700) {
          ctx.fillText("GAME OVER", stage.w / 2 + 400, stage.h / 3);
          ctx.font = "bold 20px arial";
          ctx.fillText("SCORE: " + score, stage.w / 2 + 400, stage.h / 2);
        } else {
          ctx.fillText("GAME OVER", stage.w / 2, stage.h / 3);
          ctx.font = "bold 20px arial";
          ctx.fillText("SCORE: " + score, stage.w / 2, stage.h / 2);
        }

        ctx.font = "bold 20px arial";
        if (screenwidth > 700) {
          ctx.fillText("PLAY AGAIN", stage.w / 2 + 400, stage.h * 0.65);
          ctx.strokeRect(
            (stage.w - 220) / 2 + 400,
            stage.h * 0.65 - 25,
            220,
            50
          );
        } else {
          ctx.fillText("PLAY AGAIN", stage.w / 2, stage.h * 0.65);
          ctx.strokeRect((stage.w - 220) / 2, stage.h * 0.65 - 25, 220, 50);
        }

        againprog += (0 - againprog) / 300;
        //
      }

      //
      ctx.strokeStyle = "#b47124";
      ctx.fillStyle = "#b47124";
      ctx.lineWidth = 2;

      if (fireact) {
        firetm++;
        if (firetm > 5) {
          cannons.push(
            new Cannon(
              pointer.x + (square.x - pointer.x) / 4.5,
              pointer.y + (square.y - pointer.y) / 1.8,
              pointer.x,
              pointer.y
            )
          );

          firetm = 0;
        }
        if (screenwidth > 700) {
          square.x = Math.floor(Math.random() * 20) - 15 + stage.w / 2 + 400;
        } else {
          square.x = Math.floor(Math.random() * 20) - 15 + stage.w / 2;
        }
        square.y = Math.floor(Math.random() * 20) - 15 + stage.h;
      } else {
        // arm.x = stage.w;
        // arm.y = stage.h;
      }

      for (var i = 0; i < cannons.length; i++) {
        var can = cannons[i];

        can.x += (can.tx - can.x) / 5;
        can.y += (can.ty - can.y) / 5;
        can.r += (0 - can.r) / 5;

        ctx.beginPath();
        ctx.arc(can.x, can.y, can.r, 0, 2 * Math.PI);
        ctx.fillStyle = "rgba(180, 113, 36,1)";
        ctx.fill();

        if (can.r < 2 && !gameover) {
          for (var a = 0; a < enemies.length; a++) {
            var en = enemies[a];
            var dx = can.x - en.x;
            var dy = can.y - en.y;
            var dis = dx * dx + dy * dy;
            if (dis < en.r * en.r) {
              // enemies.splice(a,1);
              if (!enemies[a].des) {
                enemies[a].des = true;

                score += 10;
              }
            }
          }
        }
        if (can.r < 1 && gameover) {
          if (screenwidth > 700) {
            if (
              can.x > (stage.w - 220) / 2 + 400 &&
              can.y > stage.h * 0.65 - 25 &&
              can.x < (stage.w - 220) / 2 + 220 + 400 &&
              can.y < stage.h * 0.65 - 25 + 50
            ) {
              againprog += 5;
              if (againprog > 120) {
                newGame();
                gameover = false;
              }
            }
          } else {
            if (
              can.x > (stage.w - 220) / 2 &&
              can.y > stage.h * 0.65 - 25 &&
              can.x < (stage.w - 220) / 2 + 220 &&
              can.y < stage.h * 0.65 - 25 + 50
            ) {
              againprog += 5;
              if (againprog > 120) {
                newGame();
                gameover = false;
              }
            }
          }
        }
        if (Math.abs(can.tx - can.x) < 1) {
          cannons.splice(i, 1);
        }
      }

      ctx.beginPath();
      ctx.moveTo(pointer.x - 20, pointer.y);
      ctx.lineTo(pointer.x + 20, pointer.y);
      ctx.strokeStyle = "rgba(180, 113, 36,1)";
      ctx.stroke();
      // ctx.fill();

      ctx.beginPath();
      ctx.moveTo(pointer.x, pointer.y - 20);
      ctx.lineTo(pointer.x, pointer.y + 20);
      ctx.stroke();

      // ctx.beginPath();
      // ctx.arc(pointer.x, pointer.y, 8, 0, 2 * Math.PI);

      // square.x = Math.floor(Math.random() * 20) - 15 + stage.w / 2;
      // square.y = Math.floor(Math.random() * 20) - 15 + stage.h;

      ctx.beginPath();
      ctx.moveTo(square.x - 70, square.y);
      ctx.lineTo(
        pointer.x + (square.x - pointer.x) / 1.5 - 20,
        pointer.y + (square.y - pointer.y) / 1.2 + 10
      );
      ctx.lineTo(
        pointer.x + (square.x - pointer.x) / 4.5 - 3,
        pointer.y + (square.y - pointer.y) / 2.2 + 10
      );
      ctx.stroke();

      ctx.beginPath();
      ctx.moveTo(square.x + 70, square.y);
      ctx.lineTo(
        pointer.x + (square.x - pointer.x) / 1.5 + 20,
        pointer.y + (square.y - pointer.y) / 1.2 + 10
      );
      ctx.lineTo(
        pointer.x + (square.x - pointer.x) / 4.5 + 3,
        pointer.y + (square.y - pointer.y) / 2.2 + 10
      );
      ctx.stroke();

      ctx.beginPath();
      ctx.moveTo(square.x, square.y - 50);
      ctx.lineTo(
        pointer.x + (square.x - pointer.x) / 1.5,
        pointer.y + (square.y - pointer.y) / 1.2 - 10
      );
      ctx.lineTo(
        pointer.x + (square.x - pointer.x) / 4.5 + 0,
        pointer.y + (square.y - pointer.y) / 2.2 + 10
      );
      ctx.stroke();

      ctx.beginPath();
      ctx.arc(square.x, square.y, 70, Math.PI, 0);
      ctx.fill();
      ctx.fillStyle = "rgba(180, 113, 36,1)";
      ctx.fill();
      ctx.stroke();

      ctx.beginPath();
      ctx.arc(
        pointer.x + (square.x - pointer.x) / 1.5,
        pointer.y + (square.y - pointer.y) / 1.2 + 10,
        20,
        2 * Math.PI,
        0
      );
      // ctx.fillStyle = "rgba(180, 113, 36,1)";
      // ctx.fill();
      ctx.stroke();

      ctx.beginPath();
      ctx.arc(
        pointer.x + (square.x - pointer.x) / 2,
        pointer.y + (square.y - pointer.y) / 1.45 + 10,
        11,
        2 * Math.PI,
        0
      );
      // ctx.fill();
      ctx.fillStyle = "rgba(180, 113, 36,1)";
      // ctx.fill();
      ctx.stroke();

      ctx.beginPath();
      ctx.arc(
        pointer.x + (square.x - pointer.x) / 3,
        pointer.y + (square.y - pointer.y) / 1.8 + 10,
        7,
        2 * Math.PI,
        0
      );
      // ctx.fill();
      ctx.fillStyle = "rgba(180, 113, 36,1)";
      ctx.stroke();

      ctx.beginPath();
      ctx.arc(
        pointer.x + (square.x - pointer.x) / 4.5,
        pointer.y + (square.y - pointer.y) / 2.2 + 10,
        3,
        2 * Math.PI,
        0
      );
      // ctx.fill();
      ctx.fillStyle = "rgba(180, 113, 36,1)";
      ctx.stroke();
    }
    function mousemove(e) {
      // target();
      var mxpos = (e.pageX - loffset) * scale;
      var mypos = (e.pageY - toffset) * scale;
      pointer.x = mxpos;
      pointer.y = mypos;
    }
    window.addEventListener(
      "touchmove",
      function (e) {
        e.preventDefault();
        mousemove(e.touches[0]);
      },
      true
    );
    // window.addEventListener(
    //   "touchstart",
    //   function (e) {
    //     e.preventDefault();
    //     mousestart(e.touches[0]);
    //   },
    //   true
    // );
    window.addEventListener(
      "mousemove",
      function (e) {
        //   console.log(e.pageX, e.pageY);
        mousemove(e);
      },
      true
    );
    window.requestAnimationFrame = (function () {
      return (
        window.requestAnimationFrame ||
        function (callback) {
          window.setTimeout(callback, 1000 / 60);
        }
      );
    })();

    function animated() {
      requestAnimationFrame(animated);
      target();
    }
    animated();
  }, []);

  return (
    <div>
      <canvas
        ref={canv}
        id="canvas"
        width="1280"
        height="720"

        // onLoad={load}
      ></canvas>
    </div>
  );
}
